import { lazy, Suspense } from "react";
import { Routes, Route, BrowserRouter, Navigate } from "react-router-dom";
import { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { fetch_allProduct } from "./Redux-toolkit/Features/FetchData";

//
import { Backdrop, CircularProgress } from "@mui/material";
import "@fontsource/poppins";
import "./app.css";

//
// import { Navbar } from "./subComponent/Navbar";
// import SignIn from "./loginComponent/Login";
// import SignUp from "./loginComponent/Signup";
// import Footer from "./subComponent/Footer";
// import MainPage from "./mainComponent/MainPage";
// import Store from "./mainComponent/Store";
// import AddProduct from "./loginComponent/profile/AddProduct";
// import { WishList } from "./mainComponent/Wishlist";
// import { Cart } from "./mainComponent/Cart";
// import Profile from "./loginComponent/profile/Profile";
// import ProfileSidebar from "./loginComponent/profile/profileSidebar";
// import SearchedProduct from "./subComponent/Searched_product";
// import { ProductDetail } from "./subComponent/ProductDetail";

const SignIn = lazy(() => import("./loginComponent/Login"));
const SignUp = lazy(() => import("./loginComponent/Signup"));
const Footer = lazy(() => import("./subComponent/Footer"));
const MainPage = lazy(() => import("./mainComponent/MainPage"));
const Store = lazy(() => import("./mainComponent/Store"));
const AddProduct = lazy(() => import("./loginComponent/profile/AddProduct"));
const Profile = lazy(() => import("./loginComponent/profile/Profile"));
const ProfileSidebar = lazy(() =>
  import("./loginComponent/profile/profileSidebar")
);
const SearchedProduct = lazy(() => import("./subComponent/Searched_product"));

const Navbar = lazy(() =>
  import("./subComponent/Navbar").then((module) => {
    return { default: module.Navbar };
  })
);
const WishList = lazy(() =>
  import("./mainComponent/Wishlist").then((module) => {
    return { default: module.WishList };
  })
);
const Cart = lazy(() =>
  import("./mainComponent/Cart").then((module) => {
    return { default: module.Cart };
  })
);
const ProductDetail = lazy(() =>
  import("./subComponent/ProductDetail").then((module) => {
    return { default: module.ProductDetail };
  })
);

function App() {
  const [Loading, setLoading] = useState(true);
  const [Auth, setAuth] = useState(true);
  const { auth } = useSelector((state) => state.isAuth);
  const { post } = useSelector((state) => state.addProduct);
  const { productDeleted } = useSelector((state) => state.DeleteProduct);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(fetch_allProduct());
  }, [dispatch, post, productDeleted, Auth]);

  useEffect(() => {
    setAuth(auth);
    setLoading(false);
  }, [auth]);

  useEffect(() => {
    setLoading(true);
    try {
      setAuth(auth);
      fetch(`https://graphic-card.onrender.com/api/v1/is/auth`, {
        credentials: "include",
      })
        .then((res) => res.json())
        .then((data) => {
          // console.log(`data success : ${data.success}`);
          setAuth(data.success);
          setLoading(false);
        })
        .catch((err) => console.log(err));

      // console.log("rendering");
    } catch (err) {
      console.log(err);
    }
  }, [auth]);

  // */
  if (Loading)
    return (
      <div>
        <Backdrop
          sx={{ color: "#fff", zIndex: (theme) => theme.zIndex.drawer + 1 }}
          open={Loading}
        />

        <div className=" flex justify-center align-middle  ">
          <CircularProgress color="inherit" />
        </div>
      </div>
    );

  return (
    <div className="App">
      <BrowserRouter>
        <Suspense fallback={<h1>loading...</h1>}>
          <Navbar />
          <Routes>
            <Route path="/" element={<MainPage />} />
            <Route
              path="/signUp"
              element={Auth ? <Navigate to={"/"} /> : <SignUp />}
            />
            <Route
              path="/signIn"
              element={Auth ? <Navigate to={"/"} /> : <SignIn />}
            />
            <Route path="/home" element={<MainPage />} />
            <Route path="/store" element={<Store />} />
            <Route path="/search/:id" element={<SearchedProduct />} />
            <Route
              path="/wishlist"
              element={Auth ? <WishList /> : <Navigate to={"/signIn"} />}
            />
            <Route
              path="/cart"
              element={Auth ? <Cart /> : <Navigate to={"/signIn"} />}
            />
            <Route
              path="/profile"
              element={Auth ? <ProfileSidebar /> : <Navigate to={"/signIn"} />}
            />
            <Route
              path="/profile/user-info"
              element={Auth ? <Profile /> : <Navigate to={"/signIn"} />}
            />
            <Route
              path="/profile/sell-product"
              element={Auth ? <AddProduct /> : <Navigate to={"/signIn"} />}
            />
            <Route path="/product/:id" element={<ProductDetail />} />
          </Routes>
          <Footer />
        </Suspense>
      </BrowserRouter>
    </div>
  );
}
export default App;
